<template>
  <div class="authentication">
    <div class="record-bar">
      <van-nav-bar
        :title="passTitle"
        left-arrow
        @click-left="linkHome"
      />
    </div>
    <div class="title">{{$t('inputF6P')}}</div>
    <div class="input">
      <!-- 密码输入框 -->
      <van-password-input
        :value="Password"
        :focused="showKeyboard"
        @focus="showKeyboard = true"
      />
      <!-- 数字键盘 -->
      <van-number-keyboard
        :show="showKeyboard"
        @input="onInput"
        @delete="onDelete"
        @blur="showKeyboard = false"
      />
    </div>
  </div>
</template>

<script>
export default {
  name: 'setFundPassword',
  data () {
    return {
      // 密码
      Password: '',
      // 键盘显示
      showKeyboard: true,
      keying:'',
      code:'',
      passTitle:this.$t('ChangeFundPassword')
    }
  },
  created(){
    console.log(this.$route.params.modify,'来自')
    console.log(this.$route.params.oldPass)
    if(this.$route.params.modify === 'my' || this.$route.params.modify === 'forget'){
      this.passTitle = this.$t('setFundPassword')
    }
    this.keying = this.$route.params.keying
    this.code = this.$route.params.code
  },
  methods: {
    // 键盘输入事件
    onInput (key) {
      let that = this
      this.Password = (this.Password + key).slice(0, 6)
      if (this.Password.length === 6) {
        if(this.$route.params.modify === 'modify'){
          this.$router.push({
            name: 'reconfirmFundPassword',
            params:{
              Password:this.Password,
              modify:'modify',
              oldPass:this.$route.params.oldPass
            }
          })
        }else if(this.$route.params.modify === 'my'){
          this.$router.push({
            name: 'reconfirmFundPassword',
            params:{
              modify:'my',
              Password:this.Password,
            }
          })
        }else {
          this.$router.push({
            name: 'reconfirmFundPassword',
            params:{
              Password:that.Password,
              keying:that.keying,
              code:that.code,
              modify:'forget',
            }
          })
        }
      }
    },
    // 键盘删除事件
    onDelete () {
      this.Password = this.Password.slice(0, this.Password.length - 1)
    },
    // 返回上级
    linkHome () {
        this.$router.go(-1)
    }
  }
}
</script>

<style scoped lang="scss">
  @import "../../../style/my";
</style>



